<template>
  <div class="ganguinput">
    <el-form ref="form" :rules="rules" :model="form" label-width="80px">
      <el-row type="flex" class="row-bg">
        <!--  -->
        <el-col :span="11">
          <el-form-item label="" prop="industry">
            <el-select v-model="form.industry" placeholder="">
              <el-option label="" value=""></el-option>
              <el-option label="" value=""></el-option>
              <el-option label="" value=""></el-option>
            </el-select>
          </el-form-item>
          <el-form-item label="" prop="employee.company.name">
            <el-input v-model="form.employee.company.name"></el-input>
          </el-form-item>
          <el-form-item label="" prop="employee.name">
            <el-input v-model="form.employee.name"></el-input>
          </el-form-item>
          <el-form-item label="" prop="employee.sex">          
          <el-select v-model="form.employee.sex" placeholder="">
            <el-option label="" value="0"></el-option>
            <el-option label="" value="1"></el-option>
          </el-select>
          </el-form-item>
          <!-- <el-form-item label="" prop="employee.jobNumber">
            <el-input v-model="form.employee.jobNumber"></el-input>
          </el-form-item> -->
           <el-form-item  label="" prop="employee.idCard">
            <el-input
              v-model="form.employee.idCard"         
            ></el-input>
          </el-form-item>

          <el-form-item label="" prop="employee.postJob">
            <el-input v-model="form.employee.postJob"></el-input>
          </el-form-item>
           <el-form-item  label="" prop="employee.joinDate">
            <el-date-picker
              v-model="form.employee.joinDate"
              type="date"
              value-format="yyyyMMhh"
              placeholder=""
            ></el-date-picker>
          </el-form-item>  
          <el-form-item label="" prop="employee.seniority">
            <el-input v-model="form.employee.seniority"></el-input>
          </el-form-item>
          <el-form-item  label="" prop="cashDate">
            <el-date-picker
              v-model="form.cashDate"
              type="year"
              value-format="yyyy"
              placeholder=""
            ></el-date-picker>
          </el-form-item>         
        </el-col>
        <!--  -->
        <el-col :span="13">        
          <el-form-item label-width="150px" label="" prop="employee.postArea">
            <el-select v-model="form.employee.postArea" placeholder="" prop="postArea">
              <el-option label="" value=""></el-option>
              <el-option label="" value=""></el-option>
              <el-option label="" value=""></el-option>
              <el-option label="" value=""></el-option>
              <el-option label="" value=""></el-option>
              <el-option label="" value=""></el-option>
            </el-select>
          </el-form-item>
          <el-form-item label-width="150px" label="" prop="approvalCode">
            <el-input v-model="form.approvalCode"></el-input>
          </el-form-item>
          <el-form-item label-width="150px" label="" prop="approvalMoney">
            <el-input v-model="form.approvalMoney"></el-input>
          </el-form-item>
          <el-form-item label-width="150px" label="" prop="auditPeriod">
            <el-date-picker
              v-model="form.auditPeriod"
              type="monthrange"
              align="right"
              value-format="yyyyMM"
              unlink-panels
              range-separator=""
              start-placeholder=""
              end-placeholder=""
              >
            </el-date-picker>
          </el-form-item>
          <el-form-item label-width="150px" label="" prop="sharePerformanceMoney">
            <el-input v-model="form.sharePerformanceMoney"></el-input>
          </el-form-item>
           <el-form-item label-width="150px" label="" prop="shareMonthNumber">
            <el-input v-model="form.shareMonthNumber"></el-input>
          </el-form-item>     
          <el-form-item label-width="150px" label="" prop="shareRatio" >
            <el-input v-model="form.shareRatio" placeholder=":0.14"></el-input>
          </el-form-item> 
          <el-form-item label-width="150px" label="" prop="auditMonthNumber">
            <el-input v-model="form.auditMonthNumber"></el-input>
          </el-form-item>  
        </el-col>
      </el-row>
      <el-form-item style="background:white" label="">
        <el-input type="textarea" v-model="form.desc"></el-input>
      </el-form-item>
      <el-form-item style="background:white">
        <el-button type="primary" @click="onSubmit('form')"></el-button>
         <el-button type="primary" @click="exitinput"></el-button>
      </el-form-item>
    </el-form>
  </div>
</template>
<script>
export default {
  data() {
    return {
      form: {
        approvalCode: "", // 
        industry: "", // 
        shareMonthNumber:"", // 
        sharePerformanceMoney:"", //
        shareRatio:"", //  0.14       
        approvalMoney: "", // 
        auditMonthNumber:"", // 
        cashDate: "", //        
        auditPeriod: "", // 
         employee: {         
          idCard: "",  // 
          joinDate: "", // 
          name: "", // 
          postArea: "", // 
          // jobNumber: "", //          
          postJob: "", // 
          seniority: "", // 
          sex: "", // 
          sts: "", // 
          desc:'',// 
          company: {
             name: ""
          },
        },
      },
      rules: {
        industry: [
          { required: true, message: "", trigger: "change" },
          
        ],
        "employee.company.name": [
                  { required: true, message: '', trigger: 'blur' },
        ],
        "employee.name": [
          { required: true, message: "", trigger: "blur" },
          { min: 2, max:6, message: '2-6', trigger: 'blur' }
          ],
        "employee.sex": [{ required: true, message: "", trigger: "change" }],
        "employee.jobNumber": [{ required: true, message: "", trigger: "blur" }],
        "employee.postJob": [{ required: true, message: "", trigger: "blur" }],
        "employee.joinDate": [
          {
            type: "string",
            required: true,
            message: "",
            trigger: "blur"
          }
        ],
        auditPeriod:[{required: true, message: "", trigger: "change" }],
        "employee.seniority": [
          { required: true, message: "", trigger: "blur" },
          // { type: 'number' ,min: 2, max:6, message: '', trigger: 'blur' }
          ],
        cashDate: [
          { required: true, message: "", trigger: "blur" }
        ],       
        "employee.idCard": [
          { required: true, message: "", trigger: "blur" },
          { min: 18, max:18, message: '18', trigger: 'blur' }
          ],
        "employee.postArea": [{ required: true, message: "", trigger: "blur" }],
        approvalCode: [{ required: true, message: "", trigger: "blur" }],
        approvalMoney: [{ required: true, message: "", trigger: "blur" }],
        sharePerformanceMoney: [{ required: true, message: "", trigger: "blur" }],
        shareMonthNumber: [{ required: true, message: "", trigger: "blur" }],
        shareRatio: [{ required: true, message: "", trigger: "blur" }],
        auditMonthNumber: [{required: true, message: "", trigger: "blur" }],

      },
    };
  },
  created() {

  },
  methods: {
    change() {
      // 
      console.log("change")
    },
   exitinput(){
      this.$router.push({name:'gangu'});
   },
    // 
     onSubmit(form) {
       console.log("")
     this.$refs.form.validate((valid) => {
          if (valid) {

               console.log("",this.form)
              this.form.auditPeriod = this.form.auditPeriod.join('-');
              // console.log(JSON.stringify(this.form))
              // console.log(this.employee.id)
              // console.log("submit!");
              // console.log(JSON.stringify(this.form))
            this.$axios
              .post("/performanceShares/add", 
                JSON.stringify(this.form),
              )
              .then(res => {
                console.log("res",res,"res");
                this.$message.info(res.data.msg);
              })
              .catch(erro => {
                console.log(erro);
              });   

          } else {
            console.log('error submit!!');
            this.$message.info(",");
            return false;
          }
        });   

    },   
  }
};
</script>

<style lang="scss" scoped>
</style>



