<template>
  <div class="qiquanaccount">
    <el-form :inline="true" :model="formInline" class="demo-form-inline">
      <el-form-item label="">
        <el-input v-model="formInline.employeeName" placeholder=""></el-input>
      </el-form-item>
      <!--  -->
      <!-- <el-form-item >
        <span></span>
        <el-date-picker  v-model="countYear" type="year" placeholder=""></el-date-picker>
      </el-form-item> -->

      <el-form-item>
        <el-button type="primary" @click="search"></el-button>
        <el-button type="primary" @click="grant"></el-button>
      </el-form-item>
    </el-form>
    <el-table
      :data="tableData"
      header-align="center"
      :cell-style="cellHeight"
      fit
      @selection-change="accountSelectionChange"
    >
      <el-table-column prop="date" align="center" label="">
        <el-table-column type="selection"></el-table-column>
        <el-table-column align="center" label="">
          <template slot-scope="scope">{{ scope.row.number }}</template>
        </el-table-column>
        <el-table-column prop="employee.name" align="center" label=""></el-table-column>
        <el-table-column prop="employee.sex" align="center" label=""></el-table-column>
        <el-table-column prop="employee.idCard" align="center" label="" width="170"></el-table-column>
        <el-table-column prop="importCompany.name" align="center" label=""></el-table-column>
        <el-table-column prop="employee.postJob" align="center" label=""></el-table-column>
        <el-table-column prop="employee.postArea" align="center" width="100" label=""></el-table-column>
        <!-- <el-table-column prop="employee.jobNumber" align="center" label=""></el-table-column> -->
        <el-table-column prop="employee.seniority" align="center" label=""></el-table-column>
        <el-table-column prop="sendSts" align="center" label=""></el-table-column>
      </el-table-column>
      <el-table-column prop="province" align="center" label="">
        <!-- <el-table-column prop="equityActualMoney" align="center" width="100" label=""></el-table-column> -->
         
        <el-table-column prop="equityShouldMoney" width="100" align="center" label="" ></el-table-column>      
        <el-table-column prop="equityActualMoney" align="center" width="130" label="" ></el-table-column>
      </el-table-column>

      <el-table-column prop="province" align="center" label="">
        <template slot-scope="scope">
          <el-button type="text" @click="countDetail(scope)" size="small"></el-button>
        </template>
      </el-table-column>
    </el-table>
    <el-pagination
      background
      layout="prev, pager, next, sizes, total, jumper"
      :page-sizes="[6, 10, 15, 20]"
      :page-size="10"
      :total="this.total"
      @current-change="handleCurrentChange"
      @size-change="handleSizeChange"
    ></el-pagination>
    <!--  -->
    <div class="countDetail" v-if="isDetail">
      <div
        style="width:100%;text-align:center;height:40px;line-height:40px;border-radius:5px;background:#409EFF;margin-bottom:10px;"
      ></div>
      <el-table :data="countDetailData" border>
        <el-table-column prop="sendYear" label=""></el-table-column>
        <el-table-column prop="equityShouldMoney" label=""></el-table-column>
        <el-table-column prop="equityActualMoney" label=""></el-table-column>
      </el-table>
      <el-button type="primary" style="margin:15px 0 15px 0;" @click="countexit"></el-button>
    </div>
  </div>
</template>

<script>
import qs from "qs";
export default {
  name: "city",
  data() {
    return {
      input: "",

      // countYear: new Date().getFullYear().toString(),// 
      employeeName: "",
      actualSendNums: [],
      employeeId: "", // id
      countSendID: "", // Id
      tableData: [], // 
      countDetailData: [], // 
      ids: [], // ids
      currentPage1: 1,
      total: 0,
      page: 1,
      pageSize: 1, // 
      pageNum: 10, // 
      // loading: true,
      isDetail: false,
      formInline: {
        employeeName: ""
      }
    };
  },
  created() {
     this.getaccount();
  },
  methods: {
    cellHeight() {
      return "padding: 0 !important";
    },
    // ids
    accountSelectionChange(val) {
      console.log(val);
      this.ids = [];
      let len = val.length;
      for (let i = 0; i < len; i++) {
        this.ids.push(val[i].id);
      }
      console.log("ids", this.ids);
    },
    // 
    grant() {
      this.$axios
        .put(
          "/employeeEquityCount/send",
          qs.stringify(
            {
              countIds: this.ids,
              type: 1
            },
            { indices: false }
          )
        )
        .then(res => {
          this.$message({
            message: res.data.msg
          });
          this.getaccount();
          console.log("", res);
        })
        .catch(erro => {
          console.log("", erro);
        });
    },
    // 
    search() {
      this.pageSize = 1;
      this.pageNum = 10;
      this.$axios
        .get("/employeeEquityCount/counting/list", {
          params: {
            limit: this.pageNum,
            page: this.pageSize,
            // countYear: this.countYear,
            countYear: 2019,
            employeeName: this.formInline.employeeName
          }
        })
        .then(res => {
          console.log("", res);
          this.total = res.data.total;
          this.tableData = res.data.data;
          this.$set(this.tableData, "number");
          for (var i = 0, len = this.tableData.length; i < len; i++) {
            const employee = this.tableData[i].employee;
            this.tableData[i].number = i + 1;
            if (employee.sex == "0") {
              employee.sex = "";
            } else {
              employee.sex = "";
            }
            if (this.tableData[i].sendSts == "Y") {
              this.tableData[i].sendSts = "";
            } else if (this.tableData[i].sendSts == "N") {
              this.tableData[i].sendSts = "";
            }
          }
        })
        .catch(erro => {
          console.log("", erro);
        });
    },

    // 
    handleinput(scope) {
      console.log(scope, "scope");
      this.ids = scope.row.id;
      this.actualSendNums = scope.row.actualSend;
      console.log(this.actualSendNums, this.ids, "actualSendNums");

      // ,
      this.$confirm(", ?", "", {
        confirmButtonText: "",
        cancelButtonText: "",
        type: "warning"
      })
        .then(() => {
          this.actualSend();
        })
        .catch(() => {
          this.$message({
            type: "info",
            message: ""
          });
        });
    },

    // 
    getaccount() {
      this.pageNum = 10;
      this.pageSize = 1;
      this.$axios
        .get("/employeeEquityCount/counting/list", {
          params: {
            limit: this.pageNum,
            page: this.pageSize,
            countYear: 2019,
            // countYear: this.countYear
          }
        })
        .then(res => {
          console.log("", res);
          this.total = res.data.total;
          this.tableData = res.data.data;
          this.$set(this.tableData, "number");
          for (var i = 0, len = this.tableData.length; i < len; i++) {
            const employee = this.tableData[i].employee;
            this.tableData[i].number = i + 1;
            if (employee.sex == "0") {
              employee.sex = "";
            } else {
              employee.sex = "";
            }
            if (this.tableData[i].sendSts == "Y") {
              this.tableData[i].sendSts = "";
            } else if (this.tableData[i].sendSts == "N") {
              this.tableData[i].sendSts = "";
            }
          }
        })
        .catch(erro => {
          console.log("", erro);
        });
    },
    // 
    actualSend() {
      this.$axios
        .put(
          "/employeeEquityCount/actual/send/" +
            this.ids +
            "?" +
            "actualMoney=" +
            this.actualSendNums
        )
        .then(res => {
          console.log("", res);
          // 
          this.getaccount();
          this.$message({
            type: "success",
            message: res.data.msg
          });
        })
        .catch(erro => {
          console.log("", erro);
        });
    },
    // 
    countDetail(val) {
      console.log(val, "val");
      this.isDetail = true;
      this.employeeId = val.row.employeeId;
      this.$axios
        .get("/employeeEquityCount/detail/" + this.employeeId)
        .then(res => {
          this.countDetailData = res.data.data;
          console.log("", res);
        })
        .catch(erro => {
          console.log("", erro);
        });
    },
    // 
    countexit() {
      this.isDetail = false;
    },

    // 
    countSend(val) {
      console.log(val);
      this.countSendID = val.row.id;
      this.$axios
        .put("/employeeEquityCount/send/" + this.countSendID)
        .then(res => {
          console.log("", res);
        })
        .catch(erro => {
          console.log("", erro);
        });
    },

    // 
    handleCurrentChange(cpage) {
      console.log(cpage, "cpage");
      this.pageSize = cpage;
      this.getaccount();
    },
    handleSizeChange(psize) {
      console.log(psize, "psize");
      this.pageNum = psize;
      this.getaccount();
    }
  }
};
</script>

<style lang="scss" scoped>
.qiquanaccount {
  .countDetail {
    position: absolute;
    height: 500px;
    overflow: auto;
    top: 10px;
    left: 10px;
    right: 10px;
    bottom: 0;
    z-index: 100;
    padding: 30px;
    background: white;
    box-shadow: 0 2px 12px 0 rgba(0, 0, 0, 0.1);
  }
}
</style>


