<template>
  <div class="gangumessage">
    <el-form ref="form" :rules="rules" :model="form" label-width="80px">


          <el-form-item label="" prop="industry">
            <el-input readonly v-model="form.industry"></el-input>
          </el-form-item>
 
          <el-form-item label="" prop="cashDate">
            <el-date-picker
              v-model="form.cashDate"
              type="year"
              value-format="yyyy"
              placeholder=""
            ></el-date-picker>
          </el-form-item>

    
          <el-form-item label-width="150px" label="" prop="approvalCode">
            <el-input v-model="form.approvalCode"></el-input>
          </el-form-item>
          <el-form-item label-width="150px" label="" prop="approvalMoney">
            <el-input v-model="form.approvalMoney"></el-input>
          </el-form-item>
          <el-form-item label-width="150px" label="" prop="auditPeriod">
            <el-date-picker
              v-model="form.auditPeriod"
              type="monthrange"
              align="right"
              value-format="yyyyMM"
              unlink-panels
              range-separator=""
              start-placeholder=""
              end-placeholder=""
            ></el-date-picker>
          </el-form-item>
          <el-form-item label-width="150px" label="" prop="sharePerformanceMoney">
            <el-input v-model="form.sharePerformanceMoney"></el-input>
          </el-form-item>
          <el-form-item label-width="150px" label="" prop="shareMonthNumber">
            <el-input v-model="form.shareMonthNumber"></el-input>
          </el-form-item>
          <el-form-item label-width="150px" label="" prop="shareRatio">
            <el-input v-model="form.shareRatio" placeholder=":0.14"></el-input>
          </el-form-item>
          <el-form-item label-width="150px" label="" prop="auditMonthNumber">
            <el-input v-model="form.auditMonthNumber"></el-input>
          </el-form-item>

      <el-button type="primary" @click="amend"></el-button>
      <el-button type="primary" @click="exit"></el-button>
    
    </el-form>
  </div>
</template>

<script>
export default {
   inject:['reload'],
  data() {
    return {
      employee: this.$store.state.gemployee,
      form: {
        approvalCode: this.$store.state.gemployee.approvalCode, // 
        industry: this.$store.state.gemployee.industry, // 
        shareMonthNumber: this.$store.state.gemployee.shareMonthNumber, // 
        sharePerformanceMoney: this.$store.state.gemployee.sharePerformanceMoney, //
        shareRatio: this.$store.state.gemployee.shareRatio.replace("%","")/100, //  0.14
        approvalMoney: this.$store.state.gemployee.approvalMoney, // 
        auditMonthNumber: this.$store.state.gemployee.auditMonthNumber, // 
        cashDate: this.$store.state.gemployee.cashDate, // 
        auditPeriod: this.$store.state.gemployee.auditPeriod.split("-"), // 
      },
      rules: {
        industry: [
          { required: true, message: "", trigger: "change" }
        ],
        auditPeriod: [
          { required: true, message: "", trigger: "change" }
        ],
      
        cashDate: [
          { required: true, message: "", trigger: "blur" }
        ], 
        approvalCode: [
          { required: true, message: "", trigger: "blur" }
        ],
        approvalMoney: [
          { required: true, message: "", trigger: "blur" }
        ],
        sharePerformanceMoney: [
          { required: true, message: "", trigger: "blur" }
        ],
        shareMonthNumber: [
          { required: true, message: "", trigger: "blur" }
        ],
        shareRatio: [
          { required: true, message: "", trigger: "blur" }
        ],
        auditMonthNumber: [
          { required: true, message: "", trigger: "blur" }
        ]
      }
    };
  },
  created() {
    // console.log(this.$store.state.gemployee.employeeIdCard);
  },

  methods: {
    amend() {
      console.log(this.$refs.form);
      this.$refs.form.validate(valid => {
        if (valid) {
          console.log("", this.form);
          this.form.auditPeriod = this.form.auditPeriod.join("-");
          this.form.performanceId = this.employee.performanceId;
          console.log(JSON.stringify(this.form));
          console.log(this.employee.id);
          this.$axios
            .put(
              "/performanceShares/edit/",
              JSON.stringify(this.form),
              {
                headers: {
                  "Content-Type": "application/json;charset=UTF-8"
                }
              }
            )
            .then(res => {
              console.log(res, "amend");
              // this.reload();
              this.$emit("refresh")
              this.form.shareRatio = this.form.shareRatio * 100 + "%";
               this.$store.state.gemployee = this.form;
              if (res.data.code == "400") {
                this.$message.info(res.data.msg);
              } else {
                this.$message.info(res.data.msg);
              }
            })
            .catch(erro => {
              console.log(erro);
            });
        } else {
          console.log("error submit!!");
          return false;
        }
      });
    },
    change() {
      // 
      console.log("change");
    },
    exit() {
      this.$store.state.gisshow = false;
    

    }
  }
};
</script>

<style lang="scss" scoped>
</style>


