Vue.use(Vuex);
Vue.use(ElementUI);


Vue.config.productionTip = false

Axios.defaults.baseURL = 'http://10.20.19.26:8097/equity'; //
// Axios.defaults.headers.common['zbxx-token'] = sessionStorage.getItem("token");//token,
Axios.defaults.headers.post['Content-Type'] = 'application/json'; //

// http request tokentoken

// console.log("token",token);
Axios.interceptors.request.use(
  config => {
    // let token = localStorage.getItem("token");
    let token = sessionStorage.getItem("token");
    if (token) { // tokenhttpheadertoken
      config.headers.common['zbxx-token'] = token;
    }
    return config;
  },
  err => {
    return Promise.reject(err);
  });

// http response 401token
Axios.interceptors.response.use(
  response => {
    //   console.log('',response)
    if (response.data.code == "401") {
      // token
      // this.$message.info(",")
      // console.log("1111111")
      // alert(",")
      // localStorage.removeItem("token");
      sessionStorage.removeItem("token");
      router.replace({
        name: 'login',
        query: {
          redirect: router.currentRoute.fullPath
        } //
      })
    }
    return response;
  },
  error => {
    console.log("error", error.toString());
    return Promise.reject(error.response.data)
  });
  

