 <template>
  <div class="addEmployee">
    <div class="employeeForm">
      <el-form ref="form" :rules="rules" :model="form" label-width="80px">
        <el-form-item label="" prop="company.name">
          <el-input v-model="form.company.name"></el-input>
        </el-form-item>

        <el-form-item label="" prop="name">
          <el-input v-model="form.name"></el-input>
        </el-form-item>
        <el-form-item label="" prop="idCard">
          <el-input v-model="form.idCard"></el-input>
        </el-form-item>
        <!-- <el-form-item label="" prop="jobNumber">
          <el-input v-model="form.jobNumber"></el-input>
        </el-form-item>-->
        <el-form-item label="" prop="sex">
          <el-select v-model="form.sex" placeholder="">
            <el-option label="" value="0"></el-option>
            <el-option label="" value="1"></el-option>
          </el-select>
        </el-form-item>

        <el-form-item label="" prop="postJob">
          <el-input v-model="form.postJob"></el-input>
        </el-form-item>

        <el-form-item label="" prop="joinDate">
          <el-date-picker
            v-model="form.joinDate"
            type="date"
            value-format="yyyyMMdd"
            placeholder=""
          ></el-date-picker>
        </el-form-item>
        
        <el-form-item label="" prop="postArea">
          <el-select v-model="form.postArea" placeholder="" prop="postArea">
            <el-option label="" value=""></el-option>
            <el-option label="" value=""></el-option>
            <el-option label="" value=""></el-option>
            <el-option label="" value=""></el-option>
            <el-option label="" value=""></el-option>
            <el-option label="" value=""></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="" prop="seniority">
          <el-input v-model="form.seniority"></el-input>
        </el-form-item>

        <el-form-item label="">
          <el-input type="textarea" v-model="form.desc"></el-input>
        </el-form-item>

        <el-form-item>
          <el-button type="primary" @click="onSubmit"></el-button>
          <el-button type="primary" @click="exitinput"></el-button>
        </el-form-item>
      </el-form>
    </div>
  </div>
</template>

<script>
export default {
  name: "city",
  data() {
    return {
      form: {
        company: {
          name: "" //
        },
        idCard: "", // 
        joinDate: "", // 
        name: "", //
        postArea: "", // 
        postJob: "", // 
        seniority: "", // 
        sex: "" //
      },
      rules: {
        "company.name": [
          { required: true, message: "", trigger: "blur" }
        ],
        name: [{ required: true, message: "", trigger: "blur" }],
        sex: [{ required: true, message: "", trigger: "change" }],
        postJob: [{ required: true, message: "", trigger: "blur" }],
        joinDate: [
          {
            type: "string",
            required: true,
            message: "",
            trigger: "change"
          }
        ],
         date1: [
            { type: 'date', required: true, message: '', trigger: 'change' }
          ],
        seniority: [{ required: true, message: "", trigger: "blur" }],
        idCard: [
          { required: true, message: "", trigger: "blur" },
          { min: 18, max: 18, message: "18", trigger: "blur" }
        ],
        postArea: [
          { required: true, message: "", trigger: "blur" }
        ]
      }
    };
  },
  created() {},
  methods: {
    //   
    onSubmit() {
      this.$refs.form.validate(valid => {
        if (valid) {
          console.log("submit!");
          console.log(JSON.stringify(this.form));
          this.$axios
            .post("/employee/addNewEmployee", JSON.stringify(this.form))
            .then(res => {
              console.log("res", res, "res");
              this.$message.info(res.data.msg);
            })
            .catch(erro => {
              console.log(erro);
            });
        } else {
          console.log("error submit!!");
          this.$message.info(",");
          return false;
        }
      });
    },

    // 
    exitinput() {
      this.$router.go(-1);
    }
  }
};
</script>

<style lang="scss" scoped>
.addEmployee {
  background: white;
  border-radius: 5px;
  .employeeForm {
    width: 80%;
    margin: 0 auto;
    padding-top: 20px;
  }
}
</style>


