<template>
  <div class="editEmployee">
     
    <el-form ref="form" :rules="rules" :model="form" label-width="80px">
      <el-row type="flex" class="row-bg">
        <!--  -->
        <el-col :span="11">
         
          <el-form-item label="" prop="company.name">
            <el-input v-model="form.company.name"></el-input>
          </el-form-item>
          <el-form-item label="" prop="name">
            <el-input v-model="form.name"></el-input>
          </el-form-item>
          <el-form-item label="" prop="sex">
            <el-select v-model="form.sex" placeholder="">
              <el-option label="" value="0"></el-option>
              <el-option label="" value="1"></el-option>
            </el-select>
          </el-form-item>
       
          <el-form-item label="" prop="postJob">
            <el-input v-model="form.postJob"></el-input>
          </el-form-item>
          <el-form-item label="" prop="joinDate">
            <el-date-picker
              v-model="form.joinDate"
              type="date"
              value-format="yyyyMM"
              @change="change"
              placeholder=""
            ></el-date-picker>
          </el-form-item>
          <el-form-item label="" prop="seniority">
            <el-input v-model="form.seniority"></el-input>
          </el-form-item>
        </el-col>
        <!--  -->
        <el-col :span="13">
          <el-form-item label-width="150px" label="" prop="idCard">
            <el-input v-model="form.idCard" readonly></el-input>
          </el-form-item>

          <el-form-item label-width="150px" label="" prop="postArea">
            <el-select v-model="form.postArea" placeholder="">
              <el-option label="" value=""></el-option>
              <el-option label="" value=""></el-option>
              <el-option label="" value=""></el-option>
              <el-option label="" value=""></el-option>
              <el-option label="" value=""></el-option>
              <el-option label="" value=""></el-option>
            </el-select>
          </el-form-item>
        </el-col>
      </el-row>
      <el-form-item label="">
        <el-input type="textarea" v-model="form.desc"></el-input>
      </el-form-item>
      <el-form-item>
        <el-button type="primary" @click="amend"></el-button>
        <el-button type="primary" @click="exit"></el-button>
      </el-form-item>
    </el-form>

    <el-button type="primary" @click="refresh"></el-button>


  </div>
</template>

<script>
export default {
  inject:['reload'],
  data() {
    return {
      newEmployee: this.$store.state.newEmployee,  
      form: {
        company: {
          name: this.$store.state.newEmployee.companyName //
        },
        idCard: this.$store.state.newEmployee.idCard, // 
        joinDate: this.$store.state.newEmployee.joinDate, // 
        name: this.$store.state.newEmployee.name, //
        postArea:this.$store.state.newEmployee.postArea, // 
        postJob: this.$store.state.newEmployee.postJob, // 
        seniority: this.$store.state.newEmployee.seniority, // 
        sex: this.$store.state.newEmployee.sex, //
        id: this.$store.state.newEmployee.id
      },
      rules: {
        "company.name": [
          { required: true, message: "", trigger: "blur" }
        ],
        name: [{ required: true, message: "", trigger: "blur" }],
        sex: [{ required: true, message: "", trigger: "change" }],
        postJob: [{ required: true, message: "", trigger: "blur" }],
        joinDate: [
          {
            type: "string",
            required: true,
            message: "",
            trigger: "blur"
          }
        ],
        seniority: [{ required: true, message: "", trigger: "blur" }],
        idCard: [
          { required: true, message: "", trigger: "blur" },
          { min: 18, max: 18, message: "18", trigger: "blur" }
        ],
        postArea: [
          { required: true, message: "", trigger: "blur" }
        ]
      },

    };
  },
  created() {
    console.log("")
  },
  methods: {
    amend() {
      this.$refs.form.validate(valid => {
        if (valid) {
          this.$axios
            .put(
              "/employee/edit",
              JSON.stringify(this.form),
              {
                headers: {
                  "Content-Type": "application/json;charset=UTF-8"
                }
              }
            )
            .then(res => {
              console.log(res, "amend");
              //  this.reload();
              this.$emit("refresh")
              if (res.data.code == "400") {
                this.$message.info(res.data.msg);
              } else {
                this.$message.info(res.data.msg);
              }
            })
            .catch(erro => {
              console.log(erro);
            });
        } else {
          this.$message.info(",");
          return false;
        }
      });
    },

    // 
    refresh(){
      this.reload();
    },
    

    change() {
      // 
      console.log("change");
    },
    exit() {
      this.$store.state.newEmployeeIsShow = false;
      
    }
  }
};
</script>

<style lang="scss" scoped>
</style>


