 <template>
  <div class="personMarage">
    <el-button-group style="padding-bottom: 5px">
      <el-button type="primary" @click="putAudit()">
        
      </el-button>
      <el-button type="primary" @click="removeBatch()">
        
      </el-button>
    </el-button-group>

    <el-table
      :data="list"
      border
      stripe
      highlight-current-row
      :cell-style="cellHeight"
      @selection-change="handleSelectionChange"
    >
      <el-table-column type="selection" width="55"></el-table-column>
      <el-table-column  label="" width="80">
        <template slot-scope="scope">{{ scope.row.number }}</template>
      </el-table-column>
      <el-table-column align="center" prop="name" width="100" label=""></el-table-column>
      <el-table-column align="center" prop="sex" label=""></el-table-column>
      <el-table-column align="center" prop="idCard" width="170px" label=""></el-table-column>
      <el-table-column align="center" prop="joinDate" width="130px" label=""></el-table-column>
      <el-table-column align="center" prop="postJob" width="60px" label=""></el-table-column>
      <el-table-column align="center" prop="postArea" width="130px" label=""></el-table-column>
      <el-table-column align="center" prop="seniority" width="50px" label=""></el-table-column>
      <el-table-column align="center" prop="auditSts" width="100px" label=""></el-table-column>
      <el-table-column align="center" prop="createDate" width="170px" label=""></el-table-column>
      <el-table-column align="center" fixed="right" width="140px" label="">
        <template slot-scope="scope">
          <el-button type="text" @click="handleEdit(scope.$index, scope.row)"></el-button>
        </template>
      </el-table-column>
    </el-table>
    <el-pagination
      background
      layout="prev, pager, next, sizes, total, jumper"
      :page-sizes="[6, 10, 15, 20]"
      :page-size="10"
      :total="this.total"
      @current-change="handleCurrentChange"
      @size-change="handleSizeChange"
    ></el-pagination>

    <div v-if="this.$store.state.newEmployeeIsShow" class="newEmployee">
      <NewEmployee @refresh="getPersonList"></NewEmployee>
    </div>
  </div>
</template>

<script>
import NewEmployee from "./editEmployee/index";
import qs from "qs";

// 
// import { formatDate } from "@/utils/date.js";

export default {
  components: {
    NewEmployee
  },
  name: "city",

  provide(){
    return{
      reload:this.reload
    }
  },

  data() {
    return {
      list: [],
      currentPage1: 1,
      total: 0,
      page: 1,
      pageSize: 1, // 
      pageNum: 10, // 
    };
  },
  // 
  // filters: {
  //   formatDate(time) {
  //     var date = new Date(time);
  //     return formatDate(date, "yyyy-MM-dd"); //     'yyyy : MM : dd'   2018125
  //   },
  //   formatDateTwo(time) {
  //     var date = new Date(time);
  //     return formatDate(date, "hh:mm:ss"); //   211212
  //   }
  // },

  created() {
    console.log("")
    this.getPersonList();

    // 
    // console.log(this.$root.$options.filters.formatDate(1646461131351));
  },


  methods: {
    cellHeight() {
      return "padding: 0 !important";
    },
    // , provide/inject
      reload(){
      this.$store.state.newEmployeeIsShow = false;
      this.$nextTick(function(){
        this.$store.state.newEmployeeIsShow = true;
      })
    },

    // 
    getPersonList() {
      this.$axios
        .get("/employee/list", {
          params: {
            limit: this.pageNum,
            page: this.pageSize
          }
        })
        .then(res => {
          console.log("", res);
          this.total = res.data.total;
          let employeeData = res.data.data;
          this.$set(employeeData, "number");
          for (var j = 0, len = employeeData.length; j < len; j++) {
            let filters = this.$root.$options.filters;
            employeeData[j].createDate = filters.formatDate(
              employeeData[j].createDate
            );
            employeeData[j].number = j + 1;
            if (employeeData[j].sex == "0") {
              employeeData[j].sex = "";
            } else {
              employeeData[j].sex = "";
            }
             switch (employeeData[j].auditSts) {
                 case "C":
                employeeData[j].auditSts = "";
                break;
              case "X":
                employeeData[j].auditSts = "";
                break;
              case "N":
                employeeData[j].auditSts = "";
                break;
              case "Y":
                employeeData[j].auditSts = "";
                break;
            };
            this.list = employeeData;
          }
        })
        .catch(erro => {
          console.log(erro);
        });
    },

    // 
    handleCurrentChange(cpage) {
      console.log(cpage, "cpage");
      this.pageSize = cpage;
      this.getPersonList();
    },
    handleSizeChange(psize) {
      console.log(psize, "psize");
      this.pageNum = psize;
      this.getPersonList();
    },

    // 
    handleEdit(index, row) {
      console.log(index, row);
      console.log("");
      this.$store.state.newEmployee = row;
      this.$store.state.newEmployeeIsShow = true;
    },

    //   ids
    handleSelectionChange(val) {
      this.ids = [];
      let len = val.length;
      for (let i = 0; i < len; i++) {
        this.ids.push(val[i].id);
      }
      console.log("ids", this.ids);
    },
    // 
    putAudit() {
      this.$axios
        .put(
          "/employee/putAudit",
          qs.stringify(
            {
              employeeIds: this.ids,
              type: 1
            },
            { indices: false }
          )
        )
        .then(res => {
          console.log("", res);
          this.$message.info("");
          this.getPersonList();
        })
        .catch(erro => {
          console.log(erro);
        });
    },
    // 
    removeBatch() {
      this.$axios
        .delete("/employee/removeBatch", {
          params: {
            employeeIds: this.ids,
            type: 1
          },
          paramsSerializer: params => {
            return qs.stringify(params, { indices: false });
          }
        })
        .then(res => {
          console.log("", res);
          this.$message.info(res.data.msg);
          this.getPersonList();
        })
        .catch(erro => {
          console.log(erro);
        });
    },

    // 
    // refresh2(){
    //   console.log(".......")
    // }
  }
};
</script>

<style lang="scss" scoped>
.personMarage {
  .newEmployee {
    position: absolute;
    height: 500px;
    overflow: auto;
    top: 1px;
    left: 50px;
    right: 50px;
    bottom: 60px;
    z-index: 100;
    padding: 15px;
    background: white;
    border-radius: 9px;
    border: 1px solid #ebeef5;
    box-shadow: 0 2px 12px 0 rgba(0, 0, 0, 0.1);
  }
}
</style>


