<template>
  <div class="qiquanmessage">
    <el-form ref="form" :rules="rules" :model="form" label-width="80px">
      <el-form-item label-width="150px" label="" prop="industry">
        <el-input readonly v-model="form.industry"></el-input>
      </el-form-item>

      <el-form-item label-width="150px" label="" prop="approvalCode">
        <el-input v-model="form.approvalCode"></el-input>
      </el-form-item>

      <el-form-item label-width="150px" label="" prop="approvalMoney">
        <el-input v-model="form.approvalMoney"></el-input>
      </el-form-item>

      <el-form-item label-width="150px" label="" prop="equityPeriod">
        <el-date-picker
          v-model="form.equityPeriod"
          type="monthrange"
          align="right"
          value-format="yyyyMM"
          unlink-panels
          range-separator=""
          start-placeholder=""
          end-placeholder=""
        ></el-date-picker>
      </el-form-item>

      <el-form-item label-width="150px" label="" prop="shareMoney">
        <el-input v-model="form.shareMoney"></el-input>
      </el-form-item>

      <el-form-item label-width="150px" label="" prop="cashDate">
        <el-date-picker
          v-model="form.cashDate"
          type="year"
          value-format="yyyy"
          @change="change"
          placeholder=""
        ></el-date-picker>
      </el-form-item>
      <el-form-item>
        <el-button type="primary" @click="amend"></el-button>
        <el-button type="primary" @click="exit"></el-button>
      </el-form-item>
    </el-form>
  </div>
</template>

<script>
// import axios from "axios";
export default {
  inject: ["reload"],
  data() {
    return {
      employee: this.$store.state.employee,
      form: {
        industry: this.$store.state.employee.industry, //
        approvalCode: this.$store.state.employee.approvalCode, // 
        approvalMoney: this.$store.state.employee.approvalMoney, // 
        cashDate: this.$store.state.employee.cashDate, // 
        shareMoney: this.$store.state.employee.shareMoney, // 
        equityPeriod: this.$store.state.employee.equityPeriod.split("-") // 
      },

      rules: {
         industry: [
          { required: true, message: "", trigger: "change" }
        ],
        equityPeriod: [
          { required: true, message: "", trigger: "change" }
        ],
        cashDate: [
          { required: true, message: "", trigger: "blur" }
        ],

        approvalCode: [
          { required: true, message: "", trigger: "blur" }
        ],
        approvalMoney: [
          { required: true, message: "", trigger: "blur" }
        ],
        shareMoney: [
          { required: true, message: "", trigger: "blur" }
        ]
      }
    };
  },
  created() {
    console.log(this.$store.state.employee);
    console.log(this.form);
  },
  methods: {
    amend() {
      this.$refs.form.validate(valid => {
        if (valid) {
          console.log(this.form.equityPeriod);
          this.form.equityPeriod = this.form.equityPeriod.join("-");
          this.form.basicEquityId = this.employee.basicEquityId;
          console.log(this.form, "this.form");
          this.$axios
            .put("/basicEquity/edit/", JSON.stringify(this.form), {
              headers: {
                "Content-Type": "application/json;charset=UTF-8"
              }
            })
            .then(res => {
              console.log(res, "amend");
              this.$store.state.employee = this.form;
               this.$emit("refresh")
              // this.reload();
              console.log(this.$store.state.employee,this.form,"state");
              if (res.data.code == "400") {
                this.$message.info(res.data.msg);
              } else {
                this.$message.info(res.data.msg);
              }
            })
            .catch(erro => {
              console.log(erro);
            });
        } else {
          this.$message.info(",");
          return false;
        }
      });
    },
    change() {
      // 
      console.log("change");
    },
    exit() {
      this.$store.state.isshow = false;
     
      // console.log(this.$store.state.isshow)
      // this.reload();
      // this.getlist();
    }
  }
};
</script>

<style lang="scss" scoped>
</style>


